import React from "react";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import moment from "moment";
import "react-dates/initialize";
import "react-dates/lib/css/_datepicker.css";
import themes from "../../../Theme/themes";
import { styles } from "../../styles";
import Calendar1day from "../Calendar1day";

const useStyles = makeStyles((theme) => ({
    ...styles,
    rootBg: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

const Calendar1dayStory = ({
    mode, brand, isMobile,
}) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Calendar1day">
                <Typography variant="h3">Calendar1day</Typography>
                <br />
                <Divider />
                <br />
                <Grid align="flex-start" >
                    <Grid item xs={isMobile ? 2 : 12} >
                        <Calendar1day
                            classes={classes}
                            onDateChange={() => undefined}
                            selectedDate={moment()}
                            isMobile={isMobile}
                            isOutsideRange={() => undefined}
                        />
                    </Grid>
                </Grid>

            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "rootBg",
        isMobile: false
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["rootBg", "darkBg"]
        },
        isMobile: { type: "boolean" }
    }
}

storiesOf("Components/Calendars/Calendar1day", module)
    .add("Default", (args) => <Calendar1dayStory {...args}/>, argsControls);
